.dropdown-menu {
	a {
		font-weight: normal;
	}
}

	.dropdown-menu__trigger {
		line-height: 3rem;
		img {
			vertical-align: middle;
		}
		&, &:hover, &:focus {
			color:white;
		}
	}

	// this default to be hidden at the bottom of the page and is moved with JS
	.dropdown-menu__menu {
		background-color: $grey-xlight;
		border-radius:$rounded-corner;
		border:1px solid $color-secondary;
		color:$color-primary;
		font-size:$size-med-large;
		padding:0 0.5rem;
		text-align:left;
		margin:$space 0;
		a {
			display: block;
			padding: 0.4em 0.5em;
		}
		li {
			border-bottom: 1px solid white;

			&:last-child {
				border:0;
			}
		}
		
		// only once moved into place
		.dropdown-menu & {
			display: none;
			position: absolute;
			z-index:100;
			top:100%;
			right:1rem;
			margin: 1.3rem 0 0 0;
			min-width:20rem;

			&:after {
				content:" ";
				width:1.5rem;
				height:1.5rem;
				display: block;
				position: absolute;
				transform: rotate(45deg);
				top: -.85rem;
				right:3rem;
				border:0 solid $color-secondary;
				border-top-width:1px;
				border-left-width:1px;
				background-color:inherit;
			}

			ul {
				// prevent arrow square overlap
				position:relative;
				z-index: 10;
			}
		}
	}


