.site {
	
	body {
		@extend %v-distribute;
		min-height: 100vh;
	}
}

	// this enables the elements to fill the screen height
	.site__root {
		@extend %v-grow;

		.site--full-content & {
			@extend %v-distribute;
		}
	}

	.site__main {
		@extend %v-grow;
		.theme--dark & {
			padding-top:0;
		}
	}

	.site__wrap {
		position:relative;
		width:48rem;
		max-width:100%;
		padding-left:3rem;
		padding-right:3rem;
		margin-left:auto;
		margin-right:auto;
	}

@include media-query('v-mob') {
	.site {
		body {
			min-height: 100vh;
		}
	}
	.site__root {
		flex-grow:1;
		display: flex;
		flex-direction: column;
	}
}

@include media-query('tab') {
	// no flexbox sticky footer
	html.site--footer.no-flexbox {
		&, body {
			height: 100%;
		}
		.site__root {
			min-height: 100%;
		  /* equal to footer height */
		  margin-bottom: -9rem; 
		  &:after {
			  content: " ";
			  display: block;
			}
		}
		.site__footer, .site__root:after {
			height: 11rem; 
		}
		.site--tab {
			.site__footer, .site__root:after {
				 height: 9rem; 
			}
		}
	}

	.site__main {
		min-height: 48rem;
	}

	.site--tab {
		.site__wrap {
			width:78.6rem;
		}
	}
}

html.site--full-content {
	.site__footer {
		position:fixed;
		bottom:0;
		left:0;
		width:100%;
	}
	.site__footer, .site__root:after {
		display: none;
	}
	.site__fix-bottom {
		position:fixed;
		z-index:100;
		bottom:0;
		left:0;
		width:100%;
		background-color:white;
		padding-top:$space;
		padding-bottom:3rem;
	}
}
