.spinner {
	
}

	.spinner__el {
		animation: spinner-fade-anim 1s linear infinite;
		fill: $color-primary;
	}
	
	// fallback to gif
	.no-cssanimations {
		.spinner {
			background: url(../img/spinner.gif) no-repeat center center;
			background-size:100% 100%;
		}
		.spinner__el {
			display: none;
		}
	}

@keyframes spinner-fade-anim { 0% { opacity: 1} 100% {opacity: 0} };

@for $i from 1 through 12 {
	.spinner__#{$i} {
		animation-delay: #{($i - 1)/12}s;
	}
}


@mixin timer($item, $duration, $size, $color, $border, $hover: running) {

  #{$item} { 
    width: $size;
    height: $size;
  }

  #{$item} .pie {
    width: 50%;
    height: 100%;
    transform-origin: 100% 50%;
    position: absolute;
    background: $color;
    border: #{$border};
  }

  #{$item} .spinner {
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    z-index: 20;
    border-right: none;
    animation: rota $duration + s linear infinite;
  }

  #{$item}:hover .spinner,
  #{$item}:hover .filler, 
  #{$item}:hover .mask {
    animation-play-state: $hover;    
  }

  #{$item} .filler {
    border-radius: 0 100% 100% 0 / 0 50% 50% 0; 
    left: 50%;
    opacity: 0;
    z-index: 10;
    animation: opa $duration + s steps(1,end) infinite reverse;
    border-left: none;
  }

  #{$item} .mask {
    width: 50%;
    height: 100%;
    position: absolute;
    background: inherit;
    opacity: 1;
    z-index: 30;
    animation: opa $duration + s steps(1,end) infinite;
  }

  @keyframes rota {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  @keyframes opa {
    0% { opacity: 1; }
    50%, 100% { opacity: 0; }
  }
}

.timer-spinner {
  position: relative;
  background: white;
}

.timer-spinner-wrap {
	position:relative;
	width: 15rem;
	height:15rem;
	margin:0 auto;
	img {
		position:absolute;
		top:50%;
		left:50%;
		margin-top: -4.25rem;
		margin-left: -2.6rem;
		width:5.2rem;
		height:8.5rem;
		z-index:50;
	}
}

@include timer('.timer-spinner', 3, 15rem, transparent, '5px solid '+$color-secondary);


