/* Select element layout for this example */
.field--select {
  position:relative;
}
.field--select label {
  display:block;
}

/* Wrapper element to enable positioning */
.field__select-wrap {
  position:relative;
  display: inline-block;
}
/* Set the dimensions of the replaced select element and make it transparent. */
.field__select-native {
  overflow: hidden;
  color:$color-primary;
  &.replaced {
    position: relative;
    z-index: 10;
    border: 0;
    filter: alpha(opacity=0); /* Move to IE-specific CSS file if you want to. */
    opacity: 0;
  }
}
.field__select-custom {
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  min-width: 100%;
  overflow: hidden;
}

.field__select-custom span {
  display: block;
  // height: 28px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  &:after {
    display: inline-block;
    content:" ";
    width: 1em;
    height: 1em;
    margin: 0 .2em;
    vertical-align: middle;
    background: url('../img/svg/arrow-down.svg') no-repeat center center;
    background-size: contain;
    .ie & {
      background-image: url('../img/arrow-down-white.png');
    }
  }
}

.field--select-block {
  /* Set the dimensions of the replaced select element and make it transparent. */
  .field__select-wrap {
    width:100%;
  }
  .field__select-native {
    @extend %form-field;
    width: 100%;
    height: 4.4rem;
    &:hover ~ .field__select-custom {
      border-color:$grey-light;
    }
    &:focus ~ .field__select-custom {
      border-color:$color-primary;
    }
  }
  .field__select-custom {
    transition: all .2s;
    max-width: 100%;
    @extend %form-field;
  }
  .field__select-custom span  {
    padding-right:1.5em;
    &:after {
      position: absolute;
      top:.6em;
      right:.5em;
      width:1.1em;
      background-image: url('../img/svg/arrow-down-color.svg');
    }
  }
}