.plain-list, %plain-list {
	list-style: none;
	padding:0;
	margin: 0;
}


.event-list {
	@extend %plain-list;
	margin-bottom:$space;
	> li {
		padding-bottom:.5em;
		margin-top:.5em;
		border-bottom:1px solid $grey-xlight;
		@include media-query('tab') {
			padding-bottom:1em;
			margin-top:1em;
			p {
				display: inline-block;
			}
			abbr {
				float:right;
			}
		}
	}
	p {
		margin:0;
		font-weight:bold;
	}
	abbr[title] {
		text-decoration: none;
		border-bottom: 0;
		color:inherit;
	}
}

.lined-list {
	@extend %plain-list;
	margin-bottom:$space;
	> li {
		border-bottom:1px solid $grey-xlight;
		font-weight: bold;
		padding-bottom:1em;
		margin-top:1em;
	}
	&--narrow {
		> li {
			padding-bottom:.5em;
			margin-top:.5em;
		}
	}
}


.column-list {
	@extend .cf;
	@extend %plain-list;
	margin-bottom:$space;
	> li {
		position: relative;
		border-bottom:1px solid $grey-xlight;
		padding:.5em 0;
	}
	@include media-query('tab') {
		columns:3;
		> li {
			break-inside: avoid;
			border-bottom:0;
			label {
				min-height: $line-height-ratio * 1em * 2;
			}
		}
		.no-csscolumns-breakinside & {
			columns:initial;

			> li {
				float:left;
				width:33.33%;
				padding-right:1em;
				break-inside: initial;
			}
		}
	}
}

.details-list {
	width:100%;
	&, ul {
		@extend %plain-list;
	}
	input {
		width:100%;
	}
	a {
		display: block;
	}
	li ul {
		margin-top:1em;
		margin-left:3rem;
		border-top:1px solid $grey-xlight;
		li:last-child {
			border-bottom:0;
			padding-bottom:0;
		}
	}
	li {
		border-bottom:1px solid $grey-xlight;
		text-align:left;
		margin-top:1em;
		padding-bottom:1em;
		vertical-align: baseline;
		&:first-child {
			.details-list__field,.details-list__label {
				padding-top:0;
			}
		}
	}
}

	.details-list__label {
		font-weight: normal;
		padding-right:0.5em;
		@include media-query('tab') {
			padding-right:1.5em;
		}
		&:last-child {
			padding-right:0;
		}
	}

	.details-list__field {
		font-weight: bold;
		width: 100%;
		font-size: $size-med-double-plus;
		margin-top:.25em;
	}

	.details-list__field--editing {
		.details-list__static-field {
				display:none;
		}
		.details-list__edit-form {
				display:block;
		}
	}
		.details-list__edit-btn {
			margin-left:.5em;
		}
		.details-list__read-field {

		}
		.details-list__edit-form {
			margin-top:$space;
			display:none;
		}
