.numpad {
	position:fixed;
	bottom:0;
	left:0;
	width:100%;
	z-index:200;
	background:white;
	max-height:30rem;;
	transition:max-height .4s;
	overflow:hidden;
}
body {
	transition:margin-bottom .2s;
}

.numpad--hidden {
	max-height:0;
	border-color:transparent;
}

	.numpad__contents {
		border-top:1px solid $color-secondary;
		max-width: 100%;
		margin:0 auto;
		overflow:hidden;
	}

	.numpad__nav {
		padding:0 1.5rem;
		background-color:$grey-xlight;
		text-align:left;
		button {
			font-size: $size-medium;
			line-height: 2rem;
			color: $color-primary;
			padding: .7em .5em;
			transition-property: opacity;
			&:disabled {
				opacity: .2;
			}
			img {
				vertical-align: bottom;
			}
		}
	}

	.numpad__keys {
		padding:1rem;
		button {
			width:100%;
			font-size: $size-large;
			padding:0;
			&:active {
				background-color:$grey-xlight;
			}
		}
	}

		.numpad__zero-key {
			margin-left: 33.33%;
		}

		.numpad__del-key {
			padding-right:.5rem;
		}

		.numpad__btn {
			overflow: hidden;
		}

@include media-query('landscape-mob') {
	.numpad__keys {
		padding:.5rem;
		button {
			font-size: $size-med-large;
		}
	}
	.numpad__nav {
		
		button {
			padding: .4em .5em;
		}
	}
}

@include media-query('tab') {
	.numpad {
		position:absolute;
		top:100%;
		left:0;
		bottom:auto;
		margin-top: 1rem;
	}

	.numpad--hidden {
	}
		
		.numpad__contents {
			border: 1px solid $color-secondary;
			border-radius:$rounded-corner;
		}
}
