

/*------------------------------------*\
    $BASE
\*------------------------------------*/
/**
 * Base button styles.
 *
 * 1. Allow us to better style box model properties.
 * 2. Line different sized buttons up a little nicer.
 * 3. Stop buttons wrapping and looking broken.
 * 4. Make buttons inherit font styles.
 * 5. Force all elements using beautons to appear clickable.
 * 6. Normalise box model styles.
 * 7. If the button’s text is 1em, and the button is (3 * font-size) tall, then
 *    there is 1em of space above and below that text. We therefore apply 1em
 *    of space to the left and right, as padding, to keep consistent spacing.
 * 8. Basic cosmetics for default buttons. Change or override at will.
 * 9. Don’t allow buttons to have underlines; it kinda ruins the illusion.
 */
.btn{
    display:inline-block;       /* [1] */
    vertical-align:middle;      /* [2] */
    white-space:nowrap;         /* [3] */
    font-family:inherit;        /* [4] */
    font-size:100%;             /* [4] */
    cursor:pointer;             /* [5] */
    border:none;                /* [6] */
    margin:0;                   /* [6] */
    padding-top:   0;           /* [6] */
    padding-bottom:0;           /* [6] */
    line-height:1.8;              /* [7] */
    padding-right:1em;          /* [7] */
    padding-left: 1em;          /* [7] */
    border-radius:$rounded-corner; /* [8] */
}

.btn{
    color: white;
    background-color: $color-secondary;
    border:1px solid $color-secondary;
    
    transition-property: color, background-color, border-color;
    transition-duration:  .2s;
    font-weight: 400;
    

    &,
    &:hover{
        text-decoration:none;   /* [9] */
    }
    &:active,
    &:focus{
        outline:none;
    }
}


/*------------------------------------*\
    $STYLES
\*------------------------------------*/
.btn--fill {
    color: white;
    background-color: $color-secondary;

    &:hover, &:focus {
        color:white;
        background-color: $color-secondary-hover;
    }

    &:disabled {
    	background-color:$grey-xlight;
    	color:$grey-light;
    	border-color:transparent;
    }
}

.btn--outline {
    color:$color-secondary;
    background-color:transparent;
    .theme--dark & {
        &, &:hover, &:focus {
            border-color:white;
            color:inherit;
        }
    }
    &:hover{
        color: $color-secondary-hover;
        border-color: $color-secondary-hover;
    }
    &:focus{
        color: $color-secondary-focus;
        border-color: $color-secondary-focus;
    }
    &:disabled {
    	opacity:.5;
    }
}

.btn--light {
    color: $color-primary;
    background-color: white;
    border-width:0;
    &:hover, &:focus {
        color: $color-primary-hover;
        background-color: $grey-xlight;
    }
    &:disabled {
    	opacity:.5;
    }
}

.btn--subtle {
    color: $color-secondary;
    border:0;
    background:transparent;
    font-size: $size-med-plus;
    &:hover, &:focus {
        color: $color-secondary-hover;
    }
    &:disabled {
    	opacity:.5;
    }
}

.btn--strong {
  font-weight: bold;
}

/*------------------------------------*\
    $SIZES
\*------------------------------------*/
/**
 * Button size modifiers.
 *
 * These all follow the same sizing rules as above; text is 1em, space around it
 * remains uniform.
 */
.btn--small{
    padding-right:0.5em;
    padding-left: 0.5em;
}

.btn--large{
    padding-right:1.5em;
    padding-left: 1.5em;
    @include font-size($button-large-size, false);
}

.btn--huge{
    padding-right:2em;
    padding-left: 2em;
}

/**
 * These buttons will fill the entirety of their container.
 *
 * 1. Remove padding so that widths and paddings don’t conflict.
 */
.btn--full{
    width:100%;
    padding-right:0;            /* [1] */
    padding-left: 0;            /* [1] */
    text-align:center;
}


/*------------------------------------*\
    $FONT-SIZES
\*------------------------------------*/
/**
 * Button font-size modifiers.
 */
.btn--alpha{
    font-size:3rem;
}

.btn--beta{
    font-size:2rem;
}

.btn--gamma{
    font-size:1rem;
}

/**
 * Make the button inherit sizing from its parent.
 */
.btn--natural{
    vertical-align:baseline;
    font-size:inherit;
    line-height:inherit;
    padding-right:0.3em;
    padding-left: 0.3em;
    background-color:transparent;
    border:0;
}

/**
 * These buttons will display as plain text
 */
.btn--plain {
    padding:0;
    line-height: inherit;
    font-size: inherit;
    background:0;
    border:0;
}

/**
 * Inactive, disabled buttons.
 * 
 * 1. Make the button look like normal text when hovered.
 */
.btn--inactive,
    .btn--inactive:hover,
    .btn--inactive:active,
    .btn--inactive:focus{
        background-color:#ddd;
        color:#777;
        cursor:text;            /* [1] */
    }

