.cta {
	color:$color-secondary;
	font-weight:bold;
	background:transparent;
	border:0;
	padding:0;
	line-height:inherit;
	vertical-align:bottom;
	&:hover {
		color:$color-secondary-hover;
	}
	&:focus {
		color:$color-secondary-focus;
		outline:0;
	}
	img {
		width:2.5rem;
		height:2.5rem;
		margin-left:0.2em;
		vertical-align: middle;
	}
}
