/**
 * Colors
 *
 * If possible map to a semantic var in config theme 
 * section rather than referencing color name directly in styles
 */
// Primary
$purple: #5B5377;
$purple-lighter: #7C7693;
$purple-faint: #807996;
$purple-fainter: #B3A6BE;
$purple-darker: #333141;

// Secondary

$turquoise: #00A6C9;
$turquoise-faint: #d1edf4;
$turquoise-lighter: lighten($turquoise,3);
$turquoise-darker: darken($turquoise,3);

$grey-light: #C6C6C6;
$grey-placeholder: #a9a9a9;
$grey-xlight: #f4f4f4;
$grey-xxlight: #f9f9f9;
$grey-mid: #868686;
$grey-dark: #868686;
$grey-xdark: #ECECEC;
$red: #ED2926;
$red-pink: #f74160;
