.site-footer {
	@extend %theme--dark;
	
	position:relative;
	text-align:left;
	.theme--dark & {
		background: transparent;
	}
	a {
		font-weight: normal;
		&:hover, &:focus {
			color:white;
			text-decoration: underline;
		}
	}

	&__inner {
		padding:2.5rem 3rem 2.5rem;
	}
}

@include media-query('tab') {
	.site--tab {
		.site-footer {
			text-align:center;
		}
		.site-footer__content-wrap {
			width: 80%;
			margin:0 auto;
		}
		.site-footer__links {
			> li{
				line-height: $space*2;
				width:33.33%;
				a {
					display: inline-block;
					line-height: $space;
					vertical-align: middle;
				}
			}
		}
	}
}
