	.page {

	}

	.page__copy {
		@include font-size($size-med-plus);
		text-align: center;
		width:20em;
		max-width:100%;	
		margin-left:auto;
		margin-right:auto;
	}

	.page__header {
		@extend .page__copy;
		p {
			margin-bottom:$space-half;
		}
		p:last-child {
			margin-bottom: 0;
		}
		margin-bottom: $space;
	}

		.page__heading {
			&:last-child {
				margin-bottom: 0;
			}
		}

	.page__section {
		margin-bottom: $space;

		&:last-child {
			margin-bottom:0;
		}
		@include media-query('tab') {
			margin-bottom: $space * 3;
		}
	}

	.page__illustration {
		margin:0 auto;
		padding-bottom:$space;
		width: 20rem;
		text-align:center;
		&--large {
			width: 60%;
			max-width:none;
		}
	}

.site--selfcare {
	.page__copy, .page__header {
		text-align: left;
		margin-left:0;
		width:auto;
	}
}
