.field--search {
	position:relative;
	input {
		width: 100%;
	}
	&:after {
		content:"";
		position:absolute;
		top:1.1rem;
		right:1rem;	
		width:1.8rem;
		height:1.8rem;
		background: url(../img/svg/search.svg) center center no-repeat;
		background-size:contain;
		pointer-events: none;
	}
}

