/* Start Custom checkboxes and radio buttons
================================= */
.field--checkbox,
.field--radio {
  /* Enable absolute positioning of the hidden form controls */
  position: relative;
  /* Just a bit of space. */
  margin-bottom: 0.5em;
  /*
   * Match line-height to the height of the replacement image to ensure it
   * doesn't get clipped
   */
  line-height: 2.0rem;

  /* Position and hide the real checkboxes and radio buttons */
  input {
    position: absolute;
    /* Match the image dimensions */
    width: 2.0rem;
    height: 2.0rem;
    /* Reset anything that could peek out or interfere with dimensions */
    overflow: hidden;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    opacity: 0;
    @extend .visuallyhidden;
    
    /*
     * Insert a pseudo element inside each label and give it a background
     * image that will become the custom checkbox or radio button.
     * Using inline-block lets you use vertical-align to adjust it vertically
     * as needed.
     */
    & + label:before {
      content: " ";
      display: inline-block;
      width: 2.0rem;
      height: 2.0rem;
      margin-right: .5em;
      vertical-align: top;
      background-color: rgba($grey-light, .2);
      background-repeat: no-repeat;
      border: .1rem solid transparent;
      border-radius: $rounded-corner;
      color: $color-primary;
      transition: all 0.2s;
    }

    &:focus + label:before, 
    &:hover + label:before {
      border-color: $grey-light;
      background-color: rgba($grey-light, .4);
    }

    &:checked + label:before {
      border-color: $color-primary;
      background-color: transparent;
    }
  }

  .parsley-errors-list {
    // text-align: center;
  }

}

.field__label--media-check {
  display:block;
  @extend .cf;
  &:before {
    float:left;
    margin-right:.5em;
  }
  > span {
    display: block;
    overflow:hidden;
  }
}


/*
 * Position the background image differently depending on the state of each
 * checkbox and radio button.
 */
/* RADIO BUTTONS */
.field--radio-label {
	label {
		font-size: $size-med-large;
		font-weight: normal;
		display: block;
		transition: color .2s;
	}
	input {
		visibility: hidden;
		position:absolute;
		top:0;
		right:0;
	}
	input:checked + label {
		font-weight:bold;
		color:$color-secondary;
	}
}

/*
 * Position the background image differently depending on the state of each
 * checkbox and radio button.
 */
/* RADIO BUTTONS */
.field-group--big-radio {
  list-style:none;
  padding:0;
  margin:0 auto;
  text-align: center;
  width:18rem;
  > li {
    display: inline-block;
    padding:.6rem;
    position:relative;
	  input {
	    position: absolute;
	    opacity: 0;
	  }
	  label {
	    display: block;
	    font-size: 1.7em;
	    width:7.5rem;
	    height:7.5rem;
	    line-height: 7rem;
	    padding-bottom:.5rem;
	    border: .3rem solid $color-secondary;
	    border-radius:50%;
	    text-align:center;
	    cursor: pointer;
	    transition-property: color, background-color, border-color;
	    transition-duration:.2s;
	    &:hover {
	      border-color:$color-secondary-faint;
	    }
	  }
	  input:checked + label {
	    background-color:$color-secondary;
	    color:white;
	  }
  }
}

/* CHECKBOXES */

.field--checkbox {
  input + label:before {
    background-image:url('../img/svg/tick.svg');
    background-position: center -300%; // position offscreen to avoid loading on click
    background-size: 70% auto;
    display: block;
  }

  input:checked + label:before {
    background-position: center center;
  }
}

