@charset "UTF-8";
/*----------------------------------------------------------------------------*\
	$TYPOGRAPHY
	Pragmatic & practical font sizing
	As per: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/
\*----------------------------------------------------------------------------*/

/*  Sizes
	Apply various font sizes by class
\*----------------------------------------------------------------------------*/

.size--large {
  @include font-size($size-large);
}
.size--med-large {
  @include font-size($size-large);
}
.size--medium {
  @include font-size($size-medium);
}
.size--small {
  @include font-size($size-small);
}


/*  Headings
\*----------------------------------------------------------------------------*/

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 300;
	line-height: 1.2;
	margin-bottom:$space;
	color:$color-secondary;
	.theme--dark & {
		color:white;
	}
}

h1, h2 {
	@include font-size($size-large);
}
h3, h4, h5, h6 {
	@include font-size($size-medium);
}



/**
 * Double stranded heading hierarchy
 */
.h1 {
	@extend h1;
}
.h2 {
	@extend h2;
}
.h3 {
	@extend h3;
}
.h4 {
	@extend h4;
}
.h5 {
	@extend h5;
}
.h6 {
	@extend h6;
}


/**
 * Heading groups and generic any-heading class.
 * To target any heading of any level simply apply a class of `.hN`
 * Sample markup:
<div class="hgroup">
	<h1 class="hN"></h1>
	<h2 class="hN"></h2>
</hgroup>
 */
hgroup,
.hgroup {
	.hN {
		margin-bottom: 0;
	}
}

/**
 * Links
 */

a {
	color: inherit;
	font-weight: bold;
	text-decoration: none;
	transition: color .2s; 
	> img {
		transition: opacity .2s;
	}
	&:hover {
		color: $color-primary-hover;
		> img {
			opacity: .7;
		}
	}
	&:focus {
		color: $color-primary-focus;
	}
}
