@charset "UTF-8";
/*----------------------------------------------------------------------------*\
    DEFAULTS
    Blocss' default variables. Redefine and override them in your
    application-level `_vars.scss` file.
\*----------------------------------------------------------------------------*/

/*----------------------------------------------------------------------------*\
    $CONTENTS
\*----------------------------------------------------------------------------*/

/**
 * TYPOGRAPHY                   Defines the typographic baseline
 * SPACING                      Spacing units to properly size your UI
 * RESPONSIVE                   Define your breakpoints
 * MISC
 */



/*----------------------------------------------------------------------------*\
    $TYPOGRAPHY
    Defines the typographic baseline.
    We define every size in pixels, the underlying system converts the
    respective units to the responsive REM value.
\*----------------------------------------------------------------------------*/


/**
 * Sizes (in rems, base on 10px base)
 */

$size-large: 5.0rem;
$size-med-large: 4.0rem;
$size-med-double-plus: 3.4rem;
$size-med-plus: 3.2rem;
$size-medium: 2.8rem;
$size-small: 2.4rem;

$half-font-sizes: true;

@if $half-font-sizes {
    $size-large: $size-large / 2;
    $size-med-large: $size-med-large / 2;
    $size-med-double-plus: $size-med-double-plus / 2;
    $size-med-plus: $size-med-plus / 2;
    $size-medium: $size-medium / 2;
    $size-small: $size-small / 2;
}

/**
 * Base font size & line height
 * We predefine a nice big font size, wich reads very nice on a big screen.
 * As per: http://uxdesign.smashingmagazine.com/2011/11/29/the-perfect-paragraph/
 */
$base-font-size: $size-med-plus;
$base-line-height: 2.0rem;
$line-height-ratio: $base-line-height/$base-font-size;
$size-root-scale: 62.5%;

/**
 * Base font family
 */
$face-fallback: 'Helvetica Neue', Helvetica, Arial, sans-serif;
$face-base: 'Roboto', $face-fallback;

/*----------------------------------------------------------------------------*\
    $THEME
\*----------------------------------------------------------------------------*/
// Colors
$color-primary: $purple;
$color-primary-hover: $purple-lighter;
$color-primary-focus: $purple-darker;
$color-primary-faint: $purple-faint;

$color-secondary: $turquoise;
$color-secondary-hover: $turquoise-lighter;
$color-secondary-focus: $turquoise-darker;
$color-secondary-faint: $turquoise-faint;

$color-error: $red-pink;
$color-error-dark: $red-pink;
$text-color: $color-primary;

// Sizes
$button-small-size: $size-small;
$button-normal-size: $size-med-plus;
$button-large-size: $size-med-large;
$input-size: $size-med-large;
$rounded-corner: 0.3rem;



/*----------------------------------------------------------------------------*\
    $SPACING
    Spacing units to properly size your UI
    As per: http://csswizardry.com/2011/12/measuring-and-sizing-uis-2011-style
\*----------------------------------------------------------------------------*/

/**
 * Enable vertical rhythm?
 *
 * Enabling this will ensure all margins, paddings, lineheights etc. will
 * follow the default line height.
 *
 * Disabling this will render all line-heights according
 * to the `$line-height-ratio` variable
 */
$use-vertical-rhythm: false;


/**
 * Assign our `$base-line-height` to a new spacing var for
 * more transparency
 */
$space: $base-line-height;
$space-half: $space/2;
$gutter: 2rem;
$gutter-wide: 7.5rem;




/*----------------------------------------------------------------------------*\
    $RESPONSIVE
    Define your breakpoints. The first value is the appendix that shall be used
    for your classes, the second value is the media
    query that the breakpoint fires at.
\*----------------------------------------------------------------------------*/
@function media-rem($px-val){
    
  @return (($px-val/10) / 1.6) * 1em;
}
$bp-mob-portrait:480;
$bp-tab:768;
$bp-desk:960;
$bp-v-mob:640;
$bp-ldsc-mob:400;
$breakpoints: (
    'mob-small'       '(max-width: ' + media-rem($bp-tab - 1) + ')',
    'mob'       			'(max-width: ' + media-rem($bp-tab - 1) + ')',
    'tab'       			'(min-width: ' + media-rem($bp-tab) + ')',
    'desk'      			'(min-width: ' + media-rem($bp-desk) + ')',
    'v-mob'     			'(max-height: ' + media-rem($bp-v-mob) + ')',
    'landscape-mob'   '(max-height: ' + media-rem($bp-ldsc-mob) + ')'
);





/*----------------------------------------------------------------------------*\
    $MISC
\*----------------------------------------------------------------------------*/

/**
 * Would you like to show a baseline grid? This is handy during development.
 */
$show-baseline: false;


/**
 * Would you like to show media queries? This is handy during development.
 */
$show-mq: false;


/**
 * Legacy support
 */
$legacy-support-for-ie8: false;


/**
 * Enable debug mode to highlight possible markup/accessibility
 * quirks in your code
 */
$debug-mode: false;
