input.parsley-success,
select.parsley-success,
textarea.parsley-success {}

input.parsley-error,
select.parsley-error,
textarea.parsley-error,
.parsley-error ~ .field__select-custom {
  &, &:focus {
    border-color: $color-error;
    .theme--dark & {
      border-color: $color-error-dark;
      border-color: rgba($color-error-dark, 0.6);
    }
  }
}
.error-copy {
  color:$color-error;
  text-align:center;
  font-size: 1.6rem;
  a {
    color:inherit;
  }
  .parsley-errors-list {
    margin-bottom: $space;
  }
  .site--selfcare & {
	  text-align:left;
  }
}
.parsley-errors-list {
  display: block;
  color:$color-error;
  margin: .2rem 0 .3rem;
  padding: 0;
  opacity: 0;
  list-style: none;
  font-size:$size-medium;
  font-weight: normal;
  transition: all .3s ease-in;
  .theme--dark & {
    background-color: $color-error-dark;
    background-color: rgba($color-error-dark, 0.6);
    border-radius: $rounded-corner;
    color: white;
    padding: .3rem 1rem ;
    margin-top: .7rem;
  }
}

.parsley-errors-list.filled {
  opacity: 1;
}
