.site-header {
	@extend %theme--dark;
	
	position:relative;
	text-align:center;

	.theme--dark & {
		background: transparent;
	}
	
	&__inner {
		padding:1.5rem 3rem 2rem;
	}
	
	&__logo {
		width:8.5rem;
		height:3.8rem;
	}

	&__powered-by {
		font-size:$size-medium;
		font-weight: 300;
		line-height: 2rem;
		margin-top: $space-half;
		margin-bottom: 0;
		img {
			vertical-align: middle;
			margin-left:.5em;
		}
	}

	&__lang-menu {
		position: absolute;
		top:1rem;
		right:1rem;
		font-size: $size-medium;
		text-align: right;
		transition: opacity .2s;
		line-height: 1;
		input, select {
			font-size: $size-small;
			line-height: 1.5 * $size-small;
			vertical-align: bottom;
		}
		.js & {
			opacity: .7;
			&:hover, &:focus {
				opacity: 1;
			}
		}
	}

	&__account-menu {
		position: absolute;
		bottom:1.5rem;
		right:1.5rem;
		> a {
			display: block;
		}
		.js & > a {
			transition: opacity .2s;
			opacity: .7;
			&:hover, &:focus {
				opacity: 1;
			}
		}
	}
}

