
/**
 * Group sets of form fields in a list, e.g.:
 *
   <ul class=form-fields>
       <li>
           <label />
           <input />
       </li>
       <li>
           <label />
           <select />
       </li>
       <li>
           <label />
           <input />
       </li>
   </ul>
 *
 */
.form-fields{
    list-style:none;
    margin:0;
    input[type='text'],
		input[type='email'],
		input[type='password'],
		input[type="tel"],
		input[type='number'],
		select {
			display: block;
			width:100%;
		}
}
    .form-fields > li{
        margin-bottom: $space;
    }
    .form-fields > li:last-child{
        margin-bottom:0;
    }
%form-field,
.text-field,
input[type='text'],
input[type='email'],
input[type='password'],
input[type="tel"],
input[type='number'] {
	@include font-size($size-med-large);
	border-radius:.3rem;
	font-weight: 300;
	padding: .35em .5em .35em;
	background: rgba($grey-light, .2);
	border:1px solid transparent;
	transition: border-color .2s;
	&:hover {
		border-color: $grey-light;
	}
	&:focus, &.focus {
		border-color: $color-primary;
		outline:none;
	}
	.theme--dark & {
		border-color:transparent;
		&:hover {
			border-color: $grey-light;
		}
		&:focus, &.focus {
			border-color: white;
		}
		&::placeholder {
			color: white;
		}
	}
}

input[type='password'] {
	// ensures larger password dot and con
	&, &.field__pin::placeholder {
		font-family: sans-serif;
	}
}

input[type='number'] {
    -moz-appearance:textfield;
}

/* Webkit browsers like Safari and Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

select {
	cursor: pointer;
}

input, select, textarea, button {
	font-family: inherit;
}

label {
	display:block;
	font-size:$size-medium;
	padding-bottom:.2rem;
}

.form-fit {
	&__expand {
		position: relative;
		min-height: 16.8rem;
	  .no-flexbox & {
			min-height: 30rem;
	  }
	}
	&__content {
		position:absolute;
		top:0;
		left:0;
		height:100%;
		width:100%;
		overflow-y: scroll; /* has to be scroll, not auto */
	  -webkit-overflow-scrolling: touch;
	  .no-flexbox & {
			// position:static;
			// top:auto;
			// left:auto;
			// height:auto;
			// width:auto;
			// overflow-y: auto;
	  }
	}
}
