
/* Arrows */
.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 3rem;
    width: 2rem;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent no-repeat center center;
    background-size:contain;
    top: 53%;
    margin-top: -1.5rem;
    padding: 0;
    border: none;
    outline: none;
    z-index:100;
    &:hover, &:focus {
        outline: none;
        opacity: .8;
    }
    &.slick-disabled {
        opacity: .3;
    }
}

.slick-prev {
  left:0;
  background-image:url('../img/svg/arrow-left.svg');
}

.slick-next {
  right:0;
  background-image:url('../img/svg/arrow-right.svg');
}

/* Dots */
.slick-dots
{
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin:$space 0;
    line-height: 1.4rem;
}
.slick-dots li
{
    position: relative;
    display: inline-block;
    padding: 0;
    cursor: pointer;
    margin: 0 .5rem;
    vertical-align: bottom;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 1.4rem;
    height: 1.4rem;
    padding: .5rem;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background-color: $grey-light;
    border-radius: 50%;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
    background-color: $color-primary-hover;
}
.slick-dots li.slick-active button
{
  background-color: $color-primary;
}
