.landing-page {
	text-align:center;
}

	.landing-page__header {
		padding-bottom: $space * 2;
	}

	.landing-page__points {
		border-top: 1px solid $grey-xlight;
		padding-top:$space * 2;
	}

@include media-query('mob') {
	.landing-page__points {
		> .grid {
			margin-left:0;
		}
		.grid__item {
			padding-left:0;
		}
	}
}

@include media-query('tab') {
	.landing-page {
		text-align: left;
	}

	.landing-page__header {
	}

	.landing-page__points {
		border-top:0;

	}

	.landing-page__point-heading {
		border-bottom: 1px solid $grey-xlight;
	}
}
