.field--pin {
	position:relative;
	width: 100%;
}

.field__pin {
	position:relative;
	width:100%;
}


.field__pin-read {
	@extend %form-field;
	overflow:hidden;
	color:$color-primary;
	font-family:sans-serif;
}

.field__pin-placeholder {
	color:$grey-placeholder;
	height:inherit;
}

.field--pin-large {
	text-align:center;
	margin-bottom:$space;

	.field__pin-read, .field__pin {
		background:$grey-xxlight;
		color:$color-secondary;
		padding:0;
		border:0;
		text-align:center;
		font-size: 6rem;
		line-height: 8rem;
		height:8rem;
		letter-spacing: .15em;
		padding-left: .15em;
		font-family:sans-serif;
	}
	
	.field__pin-placeholder, .field__pin::placeholder {
		color:$color-secondary-faint;
	}
}
