/**
 *
 * Hiding
 *
 */

	/*
	 * Hide only visually, but have it available for screen readers:
	 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
	 */

	.visuallyhidden {
		border: 0;
		clip: rect(0 0 0 0);
		height: 1px;
		margin: -1px;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 1px;
	}

	/*
	 * Extends the .visuallyhidden class to allow the element
	 * to be focusable when navigated to via the keyboard:
	 * https://www.drupal.org/node/897638
	 */

	.visuallyhidden.focusable:active,
	.visuallyhidden.focusable:focus {
		clip: auto;
		height: auto;
		margin: 0;
		overflow: visible;
		position: static;
		width: auto;
	}

	/*
	 * Hide accessibly only when js is present
	 */
	.js .js-visuallyhidden {
		@extend .visuallyhidden;
	}

	/*
	 * Hide visually and from screen readers, but maintain layout
	 */

	.invisible {
		visibility: hidden;
	}

	.js .js-transparent {
		filter: alpha(opacity=0); 
		opacity: 0;
	}

	.hide {
		display: none;
	}

/**
 *
 * Text
 *
 */


.inherit {
	&, &:hover, &:focus {
		font:inherit;
		color:inherit;
	}
}

/**
 *
 * Alignment
 *
 */
.h-center, %h-center {
	margin-left:auto;
	margin-right:auto;
}

.align--center {
	text-align: center !important;
}

.pull--right {
	float:right;
}

.pull--left {
	float:left;
}

/**
 *
 * Spacing
 *
 */

.flush {
	margin:0;
}
.space {
	margin-top:$space !important;
	margin-bottom:$space !important;
}

.space--double {
	margin-top:$space * 2 !important;
	margin-bottom:$space * 2 !important;
}

.space--top {
	margin-top:$space !important;
}
.space--bottom {
	margin-bottom:$space !important;
}

.space--half-top {
	margin-top:$space-half !important;
}
.space--half-bottom {
	margin-bottom:$space-half !important;
}

.space--double-top {
	margin-top:$space * 2 !important;
}
.space--double-bottom {
	margin-bottom:$space * 2 !important;
}

.margin--big {
	margin: 2*$space 0 !important;
}

.min-height--x1 {
	min-height: 1*$space;
}
.min-height--x2 {
	min-height: 2*$space;
}
.min-height--x3 {
	min-height: 3*$space;
}
.min-height--x4 {
	min-height: 4*$space;
}

