/**
 * Grid wrapper
 */
.grid {
	@extend .cf;
	margin-left:-$gutter;
	list-style:none;
	margin-bottom:0;
	> .grid__item {

	}
}

.grid--wide-spaced {
	margin-left:-$gutter-wide;
	> .grid__item {
		padding-left:$gutter-wide;
		&--gutter-right {
		  padding-right:$gutter;
		}
	}
}

.grid--flush {
	margin-left:0;
	> .grid__item{
		padding-left:0;
	}
}

	/**
	 * Very infrequently occuring grid wrappers as children of grid wrappers.
	 */
	.grid > .grid{
		margin-left:0;
	}


	/**
	 * Grid
	 */
	.grid__item{
		float:left;
		padding-left:$gutter;
		display:block;
		width:100%;
		vertical-align:top;
		&--flush {
		  padding-left:0 !important;
		}
		&--gutter-right {
		  padding-right:$gutter;
		}
	}

