/**
 * Carousel context specific styling
 *
 */

.slider--all {
	> .slider__slide {
	  /* pre js: only show first slide */
	  /* no js: show all, add spacing */
	  display: none;
	  &:first-child, .no-js & {
      display: block;
      display: inherit;
	  }
	  .no-js & {
      margin-bottom:$space*2;
	  }
	}
}

@include media-query(mob) {
	.slider--mob {
		margin-left:0;
		> .slider__slide {
		  /* pre js: only show first slide */
		  /* no js: show all, add spacing */
		  display: none;
		  &:first-child, .no-js & {
	      display: block;
	      display: inherit;
		  }
		  .no-js & {
	      margin-bottom:$space*2;
		  }
		}
	}
}

	.slider__slide {
		height: 28rem;
	}

	.slider__slide-inner {
		height: 100%;
	}


